                        <h1>Decoding the Fountain of Youth: 10 Ways to Live Longer</h1>
                        <h6>By | Florence Danaya Blythe<br>Page Design | Angelo Valeriano</h6>
                        <br>
                        <p>
                            &emsp;We want to live better, happier, and yes, longer. Science and technology has given us botox, age-defying moisturizers, and vitamins and medicines that keep us in the pink of health. But the truth is, there’s no sure-fire way to live longer. That, however, doesn’t mean you can’t add a few precious years to your life. Here we list down 10 easy ways to do that.
</p>
	<h2>Laugh!</h2>
    <p>
&emsp;This one is a no-brainer. How often have we heard that laughter is the best medicine? It helps ease tension, sadness, and anger. It could also do wonders for our abs because according to US cardiologist Dr. William Fry, 100 to 200 laughs are equivalent to 10 minutes of jogging or rowing. That’s good news for people who can never find the time to (or just won’t) exercise. 
</p>
	<h2>Live in the country</h2>
    <p>
&emsp;Being a city mouse has its perks: you get a higher pay (more often than not), have access to most everything you need, etcetera, etcetera. But all those luxuries coupled with an on-the-go lifestyle can take its toll on your health in the form of heart attacks, asthma, and stress. Research has shown that people living in the country have a significantly higher life expectancy than their counterparts who live in the city. A study from the Tokyo Medical and Dental School found that those live near green open spaces tend to live much longer than those stuck in the concrete jungle.
</p>
	<h2>Sleep</h2>
    <p>
&emsp;Do we even have to spell this out? Dozing off to la-la-land is a necessity because it gives your body and your heart time to recuperate from all the beatings it took from the moment your alarm sounded to the time you arrived back home. Six to eight hours of uninterrupted sleep will result to more energy and a stronger immune system.

</p>
	<h2>Be positive</h2>
    <p>
&emsp;According to researchers, having a positive attitude can add more than seven years to your life. That means you need to look more often at the bright side of things, see the glass half-full, and so on. 
</p>
	<h2>Find God</h2>
    <p>
&emsp;According to a study published in the International Journal for Psychiatry and Medicine, going to church increases life expectancy. Having a deep sense of faith showed a decrease in people’s stress and emotional problems. 
</p>
	<h2>Have a pet</h2>
    <p>
&emsp;Animal lovers are more likely to live longer compared to people who never had any pet. According to Alan Beck, Director of the Center for the Human-Animal Bond at Purdue University, contact with companion animals triggers a relaxation response. Pets, especially dogs, help make you feel happy and relaxed, which in turn lowers your blood pressure.

